ont-size: 1.4rem\r\n\r\nh3\r\n    font-size: 1rem\r\n\r\nli, input, a\r\n    border-radius: .15rem\r\n    font-size: .8rem\r\n\r\nheader\r\n    margin-top: 2rem\r\n\r\nsection\r\n    margin-top: 2rem\r\n    @include rowFlex\r\n    justify-content: space-between\r\n    align-items: stretch\r\n    gap: .4rem\r\n    white-space: nowrap\r\n    font-size: 1rem\r\n\r\n.btn\r\n    @include button\r\n\r\n.Side\r\n    flex: 1.5\r\n    gap: .5rem\r\n    @include columnFlex\r\n    justify-content: center\r\n    align-items: center\r\n    padding: .5rem .2rem\r\n    background: $secondary-color\r\n    box-shadow: $box-shadow\r\n    border-radius: .2rem\r\n    overflow: scroll\r\n    // height: 30rem\r\n\r\n    .btn\r\n        align-self: flex-end\r\n\r\nform\r\n    flex: 1\r\n    gap: .5rem\r\n    @include columnFlex\r\n    justify-content: center\r\n    align-items: stretch\r\n    padding: .5rem .2rem\r\n    background: $secondary-color\r\n    box-shadow: $box-shadow\r\n    border-radius: .2rem\r\n\r\n.fstSect\r\n    @include rowFlex\r\n    justify-content: space-between\r\n    align-items: center\r\n    gap: .5rem\r\n\r\n.recentScores\r\n    background: $secondary-color\r\n    gap: .2rem\r\n    @include columnFlex\r\n    text-align: left\r\n\r\n    li\r\n        border: 1px black solid\r\n        text-align: left\r\n        @include rowFlex\r\n        padding: .2rem\r\n        .score\r\n            background: $secondary-color\r\n            border: 0\r\n\r\ninput\r\n    padding: .2rem\r\n\r\nfooter\r\n    position: fixed\r\n    top: auto\r\n    bottom: .6rem\r\n    // width: 100%\r\n\r\n@media ( min-width: 768px  )\r\n    body\r\n        font-size: 18px\r\n        padding-left: $qlpadding\r\n        padding-right: $qrpadding\r\n\r\n    section\r\n        gap: 2rem\r\n        padding-left: $Qlpadding\r\n        padding-right: $Qrpadding\r\n@media ( min-width: 999px  )\r\n    body\r\n        font-size: 23px\r\n","$InterFont: \"Inter\", sans-serif\r\n$primary-color: #c2e2ea\r\n